<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process160"></a>

<p><br></p>
<p><span class="rvts17">捉迷藏（</span><span class="rvts50">Hide and Seek</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:331,Y:38</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：哈瑞特、本</span><span class="rvts20"></span><span class="rvts21">巴顿</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到失踪的乔伊（</span><span class="rvts20">Joe</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1128.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在浮木城的码头，可以看到两个孩子正在寻找他们的朋友乔伊（</span><span class="rvts20">Joe</span><span class="rvts21">）。根据描述，他们最后看到乔伊游泳到了河对岸去寻找被关押至欢乐堡的妈妈。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1129.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">找到一处搁浅的小鲨鱼（坐标</span><span class="rvts20">X:457</span><span class="rvts21">，</span><span class="rvts20">Y:-47</span><span class="rvts21">），与它对话后，杀掉它，并使用精灵角色吃下它掉落出的人腿。</span>
<br><span class="rvts21">之后便会了解到，乔伊被该鲨鱼吃掉了。回城告诉两个孩子，并安抚他们的心灵。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



